import React from "react";
import { HiOutlineDotsVertical } from "react-icons/hi";
import { useSelector } from "react-redux";
import styled from "styled-components";
const ViewInfo = React.lazy(() => import("./IndividualAction/ViewInfo"));
const ImportStudentInfo = React.lazy(() =>
  import("./MarksEntry/ImportStudent")
);
const AddMarks = React.lazy(() => import("./MarksEntry/AddMarks"));

const TopInformationPanel = ({ classInfo }) => {
  const [open, setOpen] = React.useState(false);
  const [openOption, setOpenOption] = React.useState(false);
  const [openMark, setOpenMark] = React.useState(false);

  const { id, flag } = useSelector((state) => state.viewInfo);

  window.onclick = (e) => {
    const optionItem = document.getElementById("optionItem");
    if (e.target.id === "option" || e.target.id === "optionIcon") {
      if (optionItem.style.display === "flex") {
        optionItem.style.display = "none";
      } else {
        optionItem.style.display = "flex";
      }
    } else {
      document.getElementById("optionItem").style.display = "none";
    }
  };

  return (
    <InfoPanel
      style={{
        background: "blue",
        minHeight: "10rem",
      }}
    >
      <OptionPanel id="option" onclick={() => setOpenOption(!openOption)}>
        <HiOutlineDotsVertical id="optionIcon" />
      </OptionPanel>

      <OptionItems id="optionItem" style={{ display: "none" }}>
        <ul
          style={{
            padding: "0.5rem",
          }}
        >
          <Li
            onClick={() => {
              setOpen(!open);
              setOpenOption(false);
            }}
          >
            Add Students
          </Li>
          <Li
            onClick={() => {
              setOpenMark(true);
              setOpenOption(false);
            }}
          >
            Add Marks
          </Li>
          <Li>Delete</Li>
          <Li>Edit</Li>
        </ul>
      </OptionItems>
      {classInfo && (
        <>
          <Title>{classInfo[0].courseName}</Title>
          <SubTitle>
            {getSemester(classInfo[0].year)} Year{" "}
            {getSemester(classInfo[0].semester)} Semester
          </SubTitle>
        </>
      )}
      <SubTitle style={{ marginTop: "auto", fontSize: "0.89rem" }}>
        {" "}
        Session: {classInfo[0].session}
      </SubTitle>
      <SubTitle style={{ marginTop: "auto", fontSize: "0.85rem" }}>
        {classInfo[0].study_center}
      </SubTitle>
      <SubTitle
        style={{
          position: "absolute",
          bottom: "10px",
          fontWeight: 400,
          fontSize: "0.8rem",
        }}
      >
        {classInfo[0].teacherName}
      </SubTitle>
      {/**
       * Open Box for Import student Name and ID
       * Download Sample File
       */}
      <ImportStudentInfo
        openBox={open}
        setOpenBox={setOpen}
        classInfo={classInfo[0]}
      />
      <AddMarks
        classInfo={classInfo[0]}
        setOpenBox={setOpenMark}
        openBox={openMark}
      />
      {flag && <ViewInfo classInfo={classInfo[0]} openBox={flag} id={id} />}
    </InfoPanel>
  );
};

export default TopInformationPanel;

const getSemester = (num) => {
  if (num === 1) {
    return "1st";
  } else if (num === 2) {
    return "2nd";
  } else if (num === 3) {
    return "3rd";
  } else if (num === 4) {
    return "4th";
  } else {
    return "";
  }
};

export const InfoPanel = styled.div`
  position: relative;
  padding: 01rem;
  width: 100%;
  border-radius: 5px;
  color: #fff;
  height: 200px;
  margin-bottom: 0.5rem;
  overflow: hidden;
`;

export const Title = styled.h1`
  font-size: 1.2rem;
`;

export const SubTitle = styled.h5`
  font-size: 1rem;
`;

export const OptionPanel = styled.div`
  position: absolute;
  right: 5px;
  top: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  width: 35px;
  height: 35px;
  font-size: 1.4rem;
  cursor: pointer;
  &:hover {
    background: rgba(240, 240, 240, 0.4);
  }
`;

export const OptionItems = styled.div`
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 35px;
  top: 15px;
  background: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 4px rgba(3, 3, 3, 0.2);
  transition: all cubic-bezier(0.075, 0.82, 0.165, 1);
`;

export const Li = styled.li`
  color: #333;
  padding: 0.3rem 0.5rem;
  list-style-type: none;
  font-weight: 500;
  cursor: pointer;

  &:nth-last-child(1) {
  }
`;
